.aboutUs {
    background-color: #f5f5f5;
    text-align: center;
    height: 100vh;
    display: flex;
    text-align: center;
    justify-content: center;
    flex-direction: column;
}

.text {
    /* font-size: 1.5rem; */
    font-weight: 500;
    color: #27253f;
    margin-bottom: 12px;
}

h2 {
    color: #27253f;
}

.contactMe {
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.flex {
    display: flex;
}

.cardContainer {
    background-color: #27253f;
    display: flex;
    flex: 1;
    justify-content: center;
}

.cardBox {
    width: 300px;
    background-color: #f5f5f5;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    margin: 10px;
    padding: 20px;
}

.repeating-before {
    list-style: none;
    padding: 0;
    margin: 0;
  }

  .repeating-before li {
    padding-left: 1rem;
    text-indent: -0.7rem;
  }

  .repeating-before li:nth-child(6n+1)::before {
    content: "üåè ";
  }

  .repeating-before li:nth-child(6n+2)::before {
    content: "‚úçüèª ";
  }

  .repeating-before li:nth-child(6n+3)::before {
    content: "üéû ";
  }

  .repeating-before li:nth-child(6n+4)::before {
    content: "üîê ";
  }

  .repeating-before li:nth-child(6n+5)::before {
    content: "üìç ";
  }
  
  .repeating-before li:nth-child(6n)::before {
    content: "‚ù§Ô∏è ";
  }

.priceBox {
    background-color: black;
    font-family: cursive;
  }

.glow {
    font-size: 42px;
    color: #fff;
    text-align: center;
    animation: glow 1s ease-in-out infinite alternate;
  }
  
  @keyframes glow {
    from {
      text-shadow: 0 0 10px #fff, 0 0 20px #fff, 0 0 30px #27253f, 0 0 40px #27253f, 0 0 50px #27253f, 0 0 60px #27253f, 0 0 70px #27253f;
    }
    
    to {
      text-shadow: 0 0 20px #fff, 0 0 30px #ff4da6, 0 0 40px #ff4da6, 0 0 50px #ff4da6, 0 0 60px #ff4da6, 0 0 70px #ff4da6, 0 0 80px #ff4da6;
    }
  }